














import { Component, Vue } from 'vue-property-decorator';
import * as collection from '@/collection.json';

    
    export default {
  methods: {
    changeImage(imageType: string) {
      const items: [] = (<any>collection)[imageType];
      const el: HTMLImageElement|null = document.querySelector('#image-card');
        if (el) {
            el.setAttribute('src', items[Math.floor(Math.random()*items.length)]);
        }
    },
  },
};
